import React from 'react';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import '../css/Header.css';
import { Link } from 'react-router-dom';
import KitchenList from '../KitchenList';
import DealsList from '../DealsList';

const responsive = {
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3,
        slidesToSlide: 3 // optional, default to 1.
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2,
        slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 2,
        slidesToSlide: 1 // optional, default to 1.
    }
};

function MyCarousel() {

    return (
        <div className="col-12 brand-header" style={{ background: 'white' }}>
            <div className="col-lg-12 ">
                <div className="col-lg-12 ">
                    <div className="col-lg-12 caraousal-header"><h4>Kitchens</h4></div>
                    {/* <div className="col-lg-6 see-all"><h5><Link to="/egapp/menu">See all</Link></h5></div> */}
                </div>
                <Carousel autoPlay={false} arrows={true} showDots={false} swipeable={true} infinite={true} autoPlaySpeed={2500} responsive={responsive} >
                    {KitchenList.map((kitchen) => (
                        <div className="card">
                            <Link to={kitchen.Link}><img className="card-img-top" src={kitchen.ImgSrc} alt="Card image cap" /></Link>
                            <div className="card-body">
                                <h5 className="card-title">{kitchen.CardTitle}</h5>
                                <p className="card-text">{kitchen.Description}</p>
                            </div>
                        </div>
                    ))}
                </Carousel>
            </div>
            <div className="col-lg-12 ">
                <div className="col-lg-12 ">
                    <div className="col-lg-12 caraousal-header"><h4>Deals Zone</h4></div>
                    {/* <div className="col-lg-6 see-all"><h5><Link to="/egapp/menu">See all</Link></h5></div> */}
                </div>
                <Carousel autoPlay={false} arrows={true} showDots={false} swipeable={true} infinite={true} autoPlaySpeed={2500} responsive={responsive} >
                    {DealsList.map((Deal) => (
                        <div className="card">
                            <Link to={Deal.Link}><img className="card-img-top" src={Deal.ImgSrc} alt="Card image cap" /></Link>
                            <div className="card-body">
                                <h5 className="card-title">{Deal.CardTitle}</h5>
                                <p className="card-text">{Deal.Description}</p>
                            </div>
                        </div>
                    ))}
                </Carousel>
            </div>
        </div>
    )
}


export default MyCarousel;